<template>
  <div class="loginPage">
    <h1>登入</h1>
    <div class="loginPage__container">
      <login></login>
    </div>
  </div>
</template>

<script>
import login from '@/components/Login.vue';

export default {
  components: { login },
  computed: {
    user() {
      let user = {};
      this.$bus.$on('son_get_data', (data) => {
        user = data.member;
      });
      this.$bus.$emit('son_call_data');
      return user;
    },
  },
  watch: {
    /* eslint-disable */
    'user.id': function () {
      if (this.user.id !== '') {
        this.go_page('/front/onlinestore/all');
      }
    },

    /* eslint-enable */
  },
  methods: {
    go_page(route) {
      this.$router.push(route);
    },
  },
  created() {
    this.$bus.$emit('toggleCart', false);
  },
};
</script>
